<h1 id="外部联系人管理" tabindex="-1"><a class="header-anchor" href="#外部联系人管理" aria-hidden="true">#</a> 外部联系人管理</h1>
<h2 id="获取实例" tabindex="-1"><a class="header-anchor" href="#获取实例" aria-hidden="true">#</a> 获取实例</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$config</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token string single-quoted-string">'corp_id'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'xxxxxxxxxxxxxxxxx'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'secret'</span>   <span class="token operator">=></span> <span class="token string single-quoted-string">'xxxxxxxxxx'</span><span class="token punctuation">,</span> 
    <span class="token operator">...</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$app</span> <span class="token operator">=</span> <span class="token class-name static-context">Factory</span><span class="token operator">::</span><span class="token function">work</span><span class="token punctuation">(</span><span class="token variable">$config</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// 基础接口</span>
<span class="token variable">$app</span><span class="token operator">-></span><span class="token property">external_contact</span><span class="token punctuation">;</span>

<span class="token comment">// 「联系我」</span>
<span class="token variable">$app</span><span class="token operator">-></span><span class="token property">contact_way</span><span class="token punctuation">;</span>

<span class="token comment">// 消息管理</span>
<span class="token variable">$app</span><span class="token operator">-></span><span class="token property">external_contact_message</span><span class="token punctuation">;</span>

<span class="token comment">// 数据统计</span>
<span class="token variable">$app</span><span class="token operator">-></span><span class="token property">external_contact_statistics</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br></div></div><h2 id="基础接口" tabindex="-1"><a class="header-anchor" href="#基础接口" aria-hidden="true">#</a> 基础接口</h2>
