<span class="token variable">$handoverUserId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'zhangsan'</span><span class="token punctuation">;</span>
<span class="token variable">$takeoverUserId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'lisi'</span><span class="token punctuation">;</span>
 
<span class="token variable">$app</span><span class="token operator">-></span><span class="token property">external_contact</span><span class="token operator">-></span><span class="token function">transfer</span><span class="token punctuation">(</span><span class="token variable">$externalUserId</span><span class="token punctuation">,</span> <span class="token variable">$handoverUserId</span><span class="token punctuation">,</span> <span class="token variable">$takeoverUserId</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
