<blockquote>
<p>{warning} 注意：</p>
<ol>
<li>通过API添加的「联系我」不会在管理端进行展示。</li>
<li>每个企业可通过API最多配置10万个「联系我」。</li>
<li>截止 2019-06-21 官方文档没有提供获取所有「联系我」列表的接口，请开发者注意自行保管处理 configId，避免无法溯源。</li>
</ol>
</blockquote>
<h3 id="增加「联系我」方式" tabindex="-1"><a class="header-anchor" href="#增加「联系我」方式" aria-hidden="true">#</a> 增加「联系我」方式</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$type</span> <span class="token operator">=</span> <span class="token number">1</span><span class="token punctuation">;</span>
<span class="token variable">$scene</span> <span class="token operator">=</span> <span class="token number">1</span><span class="token punctuation">;</span>
<span class="token variable">$config</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
   <span class="token string single-quoted-string">'style'</span> <span class="token operator">=></span> <span class="token number">1</span><span class="token punctuation">,</span>
   <span class="token string single-quoted-string">'remark'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'渠道客户'</span><span class="token punctuation">,</span>
   <span class="token string single-quoted-string">'skip_verify'</span> <span class="token operator">=></span> <span class="token constant boolean">true</span><span class="token punctuation">,</span>
   <span class="token string single-quoted-string">'state'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'teststate'</span><span class="token punctuation">,</span>
   <span class="token string single-quoted-string">'user'</span> <span class="token operator">=></span> <span class="token punctuation">[</span><span class="token string single-quoted-string">'UserID1'</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'UserID2'</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'UserID3'</span><span class="token punctuation">]</span><span class="token punctuation">,</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$app</span><span class="token operator">-></span><span class="token property">contact_way</span><span class="token operator">-></span><span class="token function">create</span><span class="token punctuation">(</span><span class="token variable">$type</span><span class="token punctuation">,</span> <span class="token variable">$scene</span><span class="token punctuation">,</span> <span class="token variable">$config</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

