<span class="token comment">//   "errcode": 0,</span>
<span class="token comment">//   "errmsg": "ok",</span>
<span class="token comment">//   "config_id":"42b34949e138eb6e027c123cba77fad7"　　</span>
<span class="token comment">// }</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br></div></div><h3 id="获取「联系我」方式" tabindex="-1"><a class="header-anchor" href="#获取「联系我」方式" aria-hidden="true">#</a> 获取「联系我」方式</h3>
